








































































































































































































import { Component, Vue } from 'vue-property-decorator'
import {
  BCard, BRow, BCol, BFormInput, BButton, BTable, BMedia, BAvatar, BLink,
  BBadge, BDropdown, BDropdownItem, BPagination,
} from 'bootstrap-vue'
import vSelect from 'vue-select'

interface IUser {
  id: string,
  name: string,
  avatar: string,
  address: string,
  address2: string,
  city: string,
  state: string,
  country: string,
  zipcode: string,
  email: string[],
  phone: string[],
  fax: string[],
  isActive: boolean,
  ehrSystem: string,
  startDate: null,
  endDate: null,
  website: string,
}

@Component({
  components: {
    BCard,
    BLink,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BTable,
    BMedia,
    BAvatar,
    BBadge,
    BDropdown,
    BDropdownItem,
    BPagination,
    vSelect,
  },
})
export default class Home extends Vue {
  // Table Handlers
  tableColumns = [
    { key: 'practice', sortable: true },
    { key: 'address', sortable: true },
    { key: 'email', label: 'contact info', sortable: false },
    { key: 'status', sortable: true },
    { key: 'actions' },
  ]

  items: IUser[] = []

  created() {
    this.fetchTableData()
  }

  isAddNewUserSidebarActive = false

  perPageOptions = [10, 25, 50, 100]

  isSortDirDesc = true

  sortBy = 'id'

  perPage = 10

  searchQuery = ''

  refUserListTable: any = null

  totalUsers = 0

  currentPage = 1

  get dataMeta() {
    const localItemsCount = this.refUserListTable ? this.refUserListTable.localItems.length : 0
    return {
      from: this.perPage * (this.currentPage - 1) + (localItemsCount ? 1 : 0),
      to: this.perPage * (this.currentPage - 1) + localItemsCount,
      of: this.totalUsers,
    }
  }

  async fetchTableData() {
    try {
      const data = await this.$http.get<{ users: IUser[] }>('/home/data')
      this.items = data.data.users as IUser[]
      this.totalUsers = this.items.length
    } catch (err) {
      console.log('err: ', err)
    }
  }
}
